body {
    font-family: 'Roboto Slab', serif; }

nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #0B4975;
    height: 50px;
    z-index: 1000;
    overflow: hidden;
    transition: all 0.3s ease-in-out;

    a {
        color: white;

        &:hover {
            color: yellow; } } }

.hamburger-button {
    display: none;
    padding: 0 15px;
    margin: 3px 0;
    border: none;
    color: white;
    font-size: 28px;

    &:hover {
        color: #33C3F0; }

    &:active,
    &:focus {
        color: white; } }

.nav-inner {
    height: 50px;
    padding-top: 3px; }

.nav-list {
    margin: 0;
    padding: 0;
    height: 50px; }

.nav-list-item {
    display: inline-block;
    padding: 0;
    margin: 0; }

a {
    text-decoration: none;
    color: black;

    &:hover {
        color: black; } }

.nav-list-link {
    padding: 10px;
    font-weight: 300;
    font-size: 16px;
    display: inline-block; }

.logo {
    font-size: 28px;
    font-weight: bold;
    display: inline-block; }

.logo-icon {
    margin-right: 10px; }

main {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-wrap: wrap;
    text-align: center; }

.social-list {
    list-style: none;
    margin: 0;
    padding: 20px 0; }

.social-list-item {
    width: 50px;
    height: 50px;
    display: inline-block;
    margin: 0 10px; }

.social-list-link {
    width: 100%;
    height: 100%;
    display: inline-block;
    font-size: 24px;
    border-radius: 50%;
    border: 2px solid gray;
    color: gray;
    line-height: 2;

    &:hover {
        border-color: white;
        color: white; } }

@media screen and (min-width: 610px) {
    .nav-list {
        float: right; } }

@media screen and (max-width: 610px) {
    nav.open {
        height: 90px; }

    .nav-list {
        float: none;
        text-align: center; }

    &.open {
        height: auto; }

    .hamburger-button {
        display: inline-block; } }
